function getContract(address, abi, library) {
  return new Contract(address, abi, library)
}

function mintDiamond(contract, amount) {
  contract.mintDiamond(amount, { value: (amount * 1).toString() + "000000000000000000" }).catch(e => {
    try {
      alert(e.data.message)

    } catch (e) {
      console.log(e)
    }
  })
}

async function getTotalSupply(contract) {
  const totalSupply = (await contract.totalSupply()).toString()
  return totalSupply
}

async function getCollection(contract, addr) {
  let promiseList = []
  const balance = parseInt((await contract.balanceOf(addr)).toString())
  for (let i = 0; i < balance; i++) {
    promiseList.push(contract.tokenOfOwnerByIndex(addr, i))
  }

  const tokenIds = await Promise.all(promiseList)
  return tokenIds

  // let promiseList2 = []
  // for (let i = 0; i < tokenIds.length; i++) {
  //   promiseList2.push(contract.tokenURI(tokenIds[i].toString()))
  // }

  // const tokenIds = await Promise.all(promiseList2)
  // return tokenIds
}

