
import styled from "styled-components";
import { useWeb3React } from '@web3-react/core'
import { InjectedConnector } from '@web3-react/injected-connector'
import abi from './abi/maercs.json'
import { Contract } from '@ethersproject/contracts'
import { formatEther } from '@ethersproject/units'
import { useEffect, useState } from "react";

import logo from "./logo.png"

const injected = new InjectedConnector({ supportedChainIds: [4002, 250] })
const StyledDiv = styled.div`
background: transparent;
height: 200px;
margin-left: 40px;
border-color: cyan;
border-width: 0px;
padding: 10px;
margin: 10px;
border-style: solid;
`

const StyledButton = styled.button`
background: transparent;
float: right;
color: white;
border: cyan;
border-radius: 100px;
border-width: medium;
border-color: white;
width: 100px;
border-style: solid;
height: 50px;
box-shadow: 0 0 20px white;
&:hover {
    background: cyan;
    color: black;
}
`

const StyledTitle = styled.div`
float: left;
font-size: 40px;
color: cyan;
`

const StyledBalance = styled.div`
float: left;
font-size: 20px;
color: cyan;
    margin-top: inherit;
`

const StyledImg = styled.img`
width: 150px;
float: left;
margin-left: calc((100% - 800px)/2);
`

function getTokenContract(address, abi, library) {
    return new Contract(address, abi, library)
}

function getBalance(contract, user) {
    return contract.balanceOf(user).then(rst => {
        return rst
    }).catch(e => console.log)
}

function NavBar(props) {
    const { connector, library, chainId, account, activate, deactivate, active, error } = useWeb3React()

    const [wooBalance, setWooBalance] = useState(0)

    useEffect(() => {
        const contract = getTokenContract(props.address, abi, library ? library.getSigner(account).connectUnchecked() : library)
        getBalance(contract, account).then(rst => {
            setWooBalance(rst)
        })
    }, [account])

    return (
        <StyledDiv id="navbar">
            <StyledImg src={logo}></StyledImg>
            {/* <div style={{width:'100%'}}></div> */}
            <b style={{display:'block',fontSize:'40px',marginTop:'80px',paddingLeft:'230px'}}>{props.title}</b>
        </StyledDiv>
    )
}

export default NavBar